.ratingTitle {
    font-size: 12px;
    font-weight: 600;
    letter-spacing: 2px;

    text-transform: uppercase;
}

.ratingStarVoteCount {
    display: flex;
    align-items: center;
}

.ratingVote {
    font-size: 20px;
    font-weight: 600;
    color: rgba(255, 255, 255);
}

.iconStar {
    display: flex;
    align-items: center;
    margin-right: 5px;
    width: 32px;
    height: 32px;
}

.ratingVoteCount {
    font-size: 12px;
    letter-spacing: 0.3px;
}